import React from 'react';

import Message from './message/message';

const ChatWindow = (props) => {
    const chat = props.chat
        .map(m => <Message 
            key={Date.now() * Math.random()}
            user={m.user}
            message={m.message}/>);
    return(
        <div className='chat_message'>
            {chat}
        </div>
    )
};

export default ChatWindow;
